<template>
  <v-app-bar
      color="primary"
      absolute
      dark
      app
      flat
      :class="_class"
      :dense="dense"
      :extended="extended"
      :extensionHeight="extensionHeight">
    <template v-slot:extension v-if="product === undefined">
      <v-container fluid>
        <v-row justify="center">
          <v-col cols="12" sm="12" md="5" class="text-center">
            <v-img :src="logo" width="100" class="mx-auto mb-5"/>
            <v-img :src="heading" width="1b0"/>
          </v-col>
        </v-row>
      </v-container>
    </template>
  </v-app-bar>
</template>

<script>
  import Logo from '../assets/coffee.png';
  import Heading from '../assets/coffee2.png';

  export default {
    name: 'AppMenu',
    data() {
      return {
        logo: Logo,
        heading: Heading,
        product: undefined,
      };
    },
    computed: {
      _class() {
        return this.product === undefined ? 'list' : '';
      },
      dense() {
        return this.product === undefined;
      },
      extended() {
        return this.product === undefined;
      },
      extensionHeight() {
        return this.product === undefined ? 230 : 0;
      }
    },
    created() {
      this.product = this.$route.params.id;
    }
  }
</script>

<style>
  header {
    background: linear-gradient(#F2BF43, #F9E184) !important;
  }

  a.title {
    text-decoration: none;
    color: white !important;
  }

  .list div.v-toolbar__content {
    height: 15px !important;
  }
</style>
